.profile_comm {
  width: 300px;
  margin: 200px auto 0;
  padding: 50px;
  font-size: 20px;
  border: 1px solid #ccc;
  border-radius: 20px;
}
.profile_comm div {
    overflow: hidden;
    padding: 5px;
}
.profile_comm h2 {
    margin: 0 0 20px;
    padding: 0;
}
.profile_comm label {
    float: left;
    width: 100px;
    font-size: 15px;
    line-height: 30px;
}
.profile_comm .wrap_inp {
    display: block;
    overflow: hidden;
    height: 30px;
}
.profile_comm .wrap_inp input {
      width: 100%;
      height: 100%;
      padding: 0 10px;
      border: 1px solid #ddd;
      box-sizing: border-box;
}
.profile_comm p {
    font-size: 12px;
    color: #aaa;
}
.profile_comm .btn_comm {
    display: block;
    width: 100%;
    height: 40px;
    margin-top: 10px;
    font-size: 20px;
    border-radius: 20px;
    background-color: #eee;
}
.profile_comm .btn_comm :disabled {
      background-color: red;
}
