.sign_comm {
  width: 300px;
  margin: 200px auto 0;
  padding: 50px;
  font-size: 20px;
  border: 1px solid #ccc;
  border-radius: 20px;
}
.sign_comm div {
    overflow: hidden;
    padding: 5px;
}
.sign_comm div strong {
      display: block;
      font-weight: normal;
      margin-bottom: 5px;
      font-size: 15px;
}
.sign_comm div .box {
      border: 1px solid #eee;
      box-sizing: border-box;
}
.sign_comm .desc_tip {
    margin: 0;
    color: cadetblue;
}
.sign_comm .desc_notice {
    color: #333;
    text-align: center;
}
.sign_comm .error {
    color: red;
}
.sign_comm h2 {
    margin: 0 0 20px;
    padding: 0;
}
.sign_comm label {
    float: left;
    width: 100px;
    font-size: 15px;
    line-height: 30px;
}
.sign_comm .wrap_inp {
    display: block;
    overflow: hidden;
    height: 30px;
}
.sign_comm .wrap_inp input {
      width: 100%;
      height: 100%;
      padding: 0 10px;
      border: 1px solid #ddd;
      box-sizing: border-box;
}
.sign_comm p {
    font-size: 12px;
    color: #aaa;
}
.sign_comm .btn_comm {
    display: block;
    width: 100%;
    height: 40px;
    margin-top: 10px;
    font-size: 20px;
    border-radius: 20px;
    background-color: #eee;
}
.sign_comm .btn_comm :disabled {
      background-color: red;
}
.sign_comm label + input {
    float: left;
    width: 38px;
    height: 30px;
    margin: 0;
}
